.launchApp {
    margin: 10px 0;
    border-radius: 34px;
    width: 130px;
    height: 30px;
    font-size: 13px;
    font-weight: bold;
    background-color: #d63939;
    border: none;
    color: #ffffff;
}

button>a {
    color: #ffffff;
}

button>a:hover {
    color: #d63939;
}

.launchApp:hover {
    border: 3px solid #d63939;
    background-color: transparent;
    color: #d63939;
}


